.project-description {
  padding: 10px 7% 10px 7%;
  height: auto;
  color: #90a4ae;
  font-size: 22px;
  line-height: 2rem;
}

.project-description .back-wrapper {
  margin-bottom: 20px;
  transition: opacity 200ms;
}

.project-description .back-wrapper div.arrow {
  position: relative;
  margin: 0 auto;
  padding: 12px 0;
  width: 40px;
}

.project-description .back-wrapper div.arrow div.bar {
  position: absolute;
  width: 24px;
  height: 4px;
  background-color: #607D8B;
  transition: transform 200ms;
}

.project-description .back-wrapper div.arrow div.bar.bar-left {
  left: 0px;
  transform: rotate(-30deg);
}

.project-description .back-wrapper div.arrow div.bar.bar-right {
  left: 18px;
  transform: rotate(30deg);
}

.project-description .back-wrapper .back {
  color: #607D8B;
  text-align: center;
}

.project-description .back-wrapper:hover {
  opacity: 0.7;
  cursor: pointer;
}

@media (max-width: 670px) {
  .project-description .back-wrapper {
    margin-bottom: 10px;
  }
}

.project-description .back-wrapper-bottom {
  margin-top: 20px;
  transition: opacity 200ms;
}

.project-description .back-wrapper-bottom div.arrow {
  position: relative;
  margin: 0 auto;
  padding: 12px 0;
  width: 40px;
}

.project-description .back-wrapper-bottom div.arrow div.bar {
  position: absolute;
  width: 24px;
  height: 4px;
  background-color: #607D8B;
  transition: transform 200ms;
}

.project-description .back-wrapper-bottom div.arrow div.bar.bar-left {
  left: 0px;
  transform: rotate(30deg);
}

.project-description .back-wrapper-bottom div.arrow div.bar.bar-right {
  left: 18px;
  transform: rotate(-30deg);
}

.project-description .back-wrapper-bottom .back {
  color: #607D8B;
  text-align: center;
}

.project-description .back-wrapper-bottom:hover {
  opacity: 0.7;
  cursor: pointer;
}

@media (max-width: 670px) {
  .project-description .back-wrapper-bottom {
    margin-bottom: 10px;
  }
}

.project-description .header {
  text-align: center;
}

.project-description .title {
  margin: 40px 0;
  font-size: 40px;
  line-height: 2.5rem;
}

.project-description .subtitle {
  font-size: 30px;
  margin: 30px 0 20px 0;
}

.project-description .subsubtitle {
  margin: 25px 0 5px 0;
  color: #607D8B;
}

.project-description .technologies {
  margin-bottom: 20px;
  color: #607D8B;
}

.project-description a {
  text-decoration: none;
  color: #e1bee7;
}

.project-description a:hover {
  text-decoration: underline;
}

.project-description ul {
  margin-left: 20px;
  list-style-type: square;
}

.project-description .epilogue {
  margin-top: 15px;
}

@media (max-width: 600px) {
  .project-description {
    font-size: 20px;
  }
}

@media (max-width: 400px) {
  .project-description {
    font-size: 18px;
  }
}

.card {
  padding: 20px;
  line-height: 160%;
}

.card-border {
  box-shadow: 0px 0px 5px 1px #b0bec5;
}

.image-card-border {
  box-shadow: 0px 0px 5px 2px #b0bec5;
}

.close-page {
  border-radius: 50%;
  background-clip: padding-box;
  box-shadow: 0px 0px 3px 0px #AAAAAA;
  background: #e1bee7;
  text-align: center;
  height: 50px;
  width: 50px;
  position: fixed;
  top: -100px;
  right: 22px;
  cursor: pointer;
  z-index: 3;
  transition: background 0.2s, transform 0.2s, top 0.5s;
}

.close-page .x {
  position: absolute;
  width: 34px;
  height: 5px;
  background: #FFFFFF;
  left: 8px;
  top: 23px;
  transition: background 0.2s;
}

.close-page .x.x-1 {
  transform: rotate(45deg);
}

.close-page .x.x-2 {
  transform: rotate(-45deg);
}

.close-page:hover {
  background: #FFFFFF;
  transform: rotate(180deg);
}

.close-page:hover .x {
  background: #e1bee7;
}

.close-page.show {
  top: 22px;
}

[ui-view].home.ng-enter, [ui-view].home.ng-leave {
  background: #FFFFFF;
  position: absolute;
  left: 0;
  right: 0;
  transition: background 0.5s opacity 0.5s;
}

[ui-view].home.ng-enter {
  background: #FFFFFF;
  opacity: 0;
}

[ui-view].home.ng-enter-active {
  background: #FFFFFF;
  opacity: 1;
}

[ui-view].home.ng-leave {
  background: #FFFFFF;
  opacity: 1;
  z-index: 0;
}

[ui-view].home.ng-leave-active {
  background: #FFFFFF;
  opacity: 1;
  z-index: 0;
}

[ui-view].animate.ng-enter, [ui-view].animate.ng-leave {
  position: absolute;
  left: 0;
  right: 0;
  transition: top 0.5s;
}

[ui-view].animate.ng-enter {
  overflow: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  height: 100%;
  width: 100%;
  opacity: 1;
  z-index: 2;
}

[ui-view].animate.ng-enter-active {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

[ui-view].animate.ng-leave {
  overflow: hidden;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

[ui-view].animate.ng-leave-active {
  overflow: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  z-index: 2;
}

.home-page {
  overflow: hidden;
  background: #FFFFFF;
}

.title-container {
  position: fixed;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  text-align: center;
}

.title-container .landing-title {
  font-family: 'Pacifico', sans-serif;
  width: 40%;
  margin: 0 auto;
  font-weight: 500;
  color: #607D8B;
  line-height: 100px;
  font-size: 90px;
  text-align: center;
  transition: font-size 0.2s ease;
}

.title-container .landing-title:hover {
  font-family: 'Roboto', sans-serif;
  font-size: 70px;
}

.title-container .icons {
  width: 40%;
  margin: 0 auto;
}

.title-container .icons a {
  text-decoration: none;
}

.title-container .icons .fa-circle {
  color: #607D8B;
  transition: all 300ms ease;
}

.title-container .icons #github:hover {
  cursor: pointer;
}

.title-container .icons #github:hover .fa-circle {
  color: #000;
}

.title-container .icons #linkedin:hover {
  cursor: pointer;
}

.title-container .icons #linkedin:hover .fa-circle {
  color: #007bb6;
}

.title-container .icons #twitter:hover {
  cursor: pointer;
}

.title-container .icons #twitter:hover .fa-circle {
  color: #00aced;
}

.title-container .icons #envelope:hover {
  cursor: pointer;
}

.title-container .icons #envelope:hover .fa-circle {
  color: #00cc00;
}

.navigation-container {
  position: fixed;
  bottom: 20%;
  width: 100%;
}

.navigation-container .navigation {
  *zoom: 1;
  margin: 0 auto;
  width: 250px;
}

.navigation-container .navigation:before, .navigation-container .navigation:after {
  content: '';
  display: table;
}

.navigation-container .navigation:after {
  clear: both;
}

.navigation-container .navigation .link {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 300;
  cursor: pointer;
  letter-spacing: 2px;
  color: #b0bec5;
  transition: color 500ms ease;
}

.navigation-container .navigation .link:hover {
  color: #607D8B;
  transform: rotate(-180deg);
  direction: rtl;
  unicode-bidi: bidi-override;
}

.navigation-container .navigation .link.left {
  float: left;
}

.navigation-container .navigation .link.right {
  float: right;
}

@media (max-width: 830px) {
  .title-container .landing-title {
    width: 60%;
  }
  .title-container .icons {
    width: 60%;
  }
}

@media (max-width: 570px) {
  .title-container .landing-title {
    width: 80%;
  }
  .title-container .icons {
    width: 80%;
  }
}

@media (max-width: 480px) {
  .title-container .landing-title {
    width: 100%;
    line-height: 90px;
    font-size: 80px;
  }
  .title-container .landing-title:hover {
    font-size: 60px;
  }
  .title-container .icons {
    width: 100%;
  }
  .navigation-container .navigation {
    width: 200px;
  }
}

.about-page .about-wrapper {
  *zoom: 1;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  padding: 0 10%;
  z-index: 1;
}

.about-page .about-wrapper:before, .about-page .about-wrapper:after {
  content: '';
  display: table;
}

.about-page .about-wrapper:after {
  clear: both;
}

.about-page .about-wrapper .overview-wrapper {
  *zoom: 1;
  float: left;
  clear: none;
  text-align: inherit;
  width: 31.33333%;
  margin-left: 0%;
  margin-right: 3%;
}

.about-page .about-wrapper .overview-wrapper:before, .about-page .about-wrapper .overview-wrapper:after {
  content: '';
