  float: right;
}

.photo-page .gif-wrapper .gif:nth-child(2n + 1) {
  clear: both;
}

@media (max-width: 880px) {
  .photo-page .gif-wrapper .gif {
    margin-bottom: 30px;
    *zoom: 1;
    float: left;
    clear: none;
    text-align: inherit;
    width: 100%;
    margin-left: 0%;
    margin-right: 3%;
  }
  .photo-page .gif-wrapper .gif:before, .photo-page .gif-wrapper .gif:after {
    content: '';
    display: table;
  }
  .photo-page .gif-wrapper .gif:after {
    clear: both;
  }
  .photo-page .gif-wrapper .gif:nth-child(2n) {
    margin-right: 3%;
    float: left;
  }
  .photo-page .gif-wrapper .gif:nth-child(2n + 1) {
    clear: none;
  }
  .photo-page .gif-wrapper .gif:nth-child(1n) {
    margin-right: 0%;
    float: right;
  }
  .photo-page .gif-wrapper .gif:nth-child(1n + 1) {
    clear: both;
  }
}

@media (max-width: 600px) {
  .photo-page .gif-wrapper .gif {
    height: 250px;
    background-size: contain;
  }
}
